import React, { useEffect, useState } from 'react';

import './Info.style.scss';
import MarketWatch from '../../assets/images/market.png'
import Bloomberg from '../../assets/images/bloomberg.png'
import Yahoo from '../../assets/images/yahoo.png'
import AnimatedDescription from '../AnimatedDescription/AnimatedDescription';

const Column = ({text1, text2}) => (
  <div className='Column_Container'>
    <p className='text1'>{text1}</p>
    <p className='text2'>{text2}</p>
  </div>
)


function Info() {
  
  const [stopSpinning, setStopSpinning] = useState(false)

  useEffect(() => {  
    const interval = setInterval(() => { 
      setStopSpinning(true)} , 1000); return () => clearInterval(interval);
    }, []
    );

  const infoData = [
    {title: "24hr Change", ammount: "-14.84%"},
    {title: "Price", ammount: "$0.0340875"},
    {title: "Volume", ammount: "$114,214.222"},
    {title: "Market Cap", ammount: "$33,917,062.5"},
  ]

  return (
    <div className='Info_Container' id='tokens'>
      <div className='row1'>
        {
          stopSpinning 
          ? infoData.map((data, i) => <Column key={i} text1={data.title} text2={data.ammount} />)
          : infoData.map(() => <div className='spinner' />)
        }  
      </div>
      <h1>As seen on</h1>
      <div className='row1'>
        <img alt="" src={MarketWatch} className='logos'/>
        <img alt="" src={Yahoo}  className='logos'/>
        <img alt="" src={Bloomberg}  className='logos'/>
      </div>
    </div>
  )
}

export default Info