import React, {useState} from 'react';

import './HomePage.style.scss';
import Header from '../../components/Header/Header';
import Info from '../../components/Info/Info';
import ImageCarousel from '../../components/ImageCarousel/ImageCarousel';
import Description from '../../components/Description/Description';
import RoadMap from '../../components/RoadMap/RoadMap';
import Stats from '../../components/Stats/Stats';
import AnimatedDescription from '../../components/AnimatedDescription/AnimatedDescription';
import TeamSection from '../../components/TeamSection/TeamSection';
import JoinTheCommunity from '../../components/JoinTheCommunity/JoinTheCommunity';
import Footer from '../../components/Footer/Footer';
import Menu from '../../assets/images/menu.png'
import Navbar from '../../components/Navbar/Navbar';

function HomePage() {
  const [showMenu, setShowMenu] = useState(false)
  const closeMenu = () => {
    setShowMenu(false)
  }
  const handleMenuClick=()=> {
    setShowMenu(!showMenu)
  }
  return (
    <div className='HomePage_Container'>
      {
      !showMenu && <img alt="menu" src={Menu}  className='menu' onClick={handleMenuClick}/>
      }
      {
      showMenu && <Navbar closeMenu={closeMenu}/>
      }
      <Header />
      <Info />
      <AnimatedDescription Span="real estate." title="A new era of digital & physical " description="Hold KEYS to buy, sell, rent, and monetize real estate and luxury assets in our metaverse. The more KEYS you hold, the more benefits and access you gain within the KEYS ecosystem." Button="Learn About LoyalKEY"/>
      <ImageCarousel />
      <Description />
      <RoadMap />
      <Stats />
      <AnimatedDescription Span="Keys Team." title="Power in numbers. Meet the" description="coming from diverse backgrounds of success in life and business, the core KEYS Token Team and our strategic partners have what it takes to bring our game-changing vision to life."/>
      <TeamSection />
      <AnimatedDescription Span="KeyNotes." title="Stay ready with our" description="Stay up to date with the latest news, partnerships, progress, and events within the KEYS ecosystem. Trust us, you dont want to miss out."/>
      <JoinTheCommunity />
      <Footer />
    </div>
  )
}

export default HomePage